.wmp-container{
    background-color: black;
    color: white;
    position: relative;
    margin-bottom: 4px;
        -webkit-touch-callout: none; /* iOS Safari */
          -webkit-user-select: none; /* Safari */ /* Konqueror HTML */
             -moz-user-select: none; /* Firefox */
              -ms-user-select: none; /* Internet Explorer/Edge */
                  user-select: none; /* Non-prefixed version, currently
                                        supported by Chrome and Opera */
}

img {
    text-indent: 100%;
    white-space: nowrap;
    overflow: hidden;
    border: 0;
}

.wmp-title-container{
    position: absolute;
    width: 100%;
    height: 60px;
    top: 0%;
    text-align: left;
    cursor: pointer;
}

.wmp-title{
    text-shadow: 0 0 2px rgba(0,0,0,.5);
    position: absolute;
    padding-left: 17px;
    padding-top: 12px;
    font-family: "YouTube Noto",Roboto,Arial,Helvetica,sans-serif;
    color: #eee;
    text-align: left;
    direction: ltr;
    font-size: 18px;
    line-height: 1.3;
    -webkit-font-smoothing: antialiased;
    -webkit-tap-highlight-color: rgba(0,0,0,0);
    white-space: nowrap;
    word-wrap: normal;
    cursor: pointer;
    z-index:10;
    color: inherit;
    text-decoration: none;
    transition: color .1s cubic-bezier(0.0,0.0,0.2,1);
    outline: 0;
}

.light-grey-to-white { 
    color: #e4e5e8; 
}
.light-grey-to-white:hover { 
    color: rgba(255, 255, 255, 1); 
}

.wmp-top-shading{
    position: absolute;
    top:0px;
    width: 100%;
    height: 60px;
    background-image: linear-gradient(to bottom, black, transparent);
    opacity:0.25;
    z-index:0;
}
.wmp-menu-bar-container{
    position: absolute;
    bottom:0px;
    height: 100%;
    width: 100%;
}

.wmp-bottom-shading{
    position: absolute;
    bottom:0px;
    width: 100%;
    height: 60px;
    background-image: linear-gradient(to top, black, transparent);
    opacity:0.25;
    z-index:0;
}

.wmp-menu-bar-offset-left{
    display: inline-block;
    height: 100%;
    left: 0%;
    width: 12px;
    position: absolute;
}

.wmp-menu-bar-offset-right{
    display: inline-block;
    height: 100%;
    width: 12px;
    right: 0%;
    position: absolute;
}

.wmp-menu-bar{
    bottom:0px;
    display: inline-block;
    left: 12px;
    right: 12px;
    position: absolute;
    z-index:5;
}

.wmp-tool-constainer{
    position: relative;
    height: 36px;
}

.wmp-tool-constainer-left{
    text-align: left;
    cursor: pointer;
    display: table;
    position: absolute;
    padding-left: 3px;
}

.wmp-tool-constainer-right{
    text-align: right;
    cursor: pointer;
    display: table;
    position: absolute;
    right: 0px;
}
.wmp-tool-button{
    display: table-cell;
    width: 38px;
    text-align: center;
    height: 36px;
    vertical-align: middle;
    padding-right: 4px;
}

.logo-padding-small { 
    padding-bottom: 1px;
}

.logo-padding-medium { 
    padding-top: 3px; 
}

.logo-padding-big { 
    padding-top: 5px; 
}

svg:hover .wmp-tool-button-logo{
    fill: white;
}

.light-grey-to-white { 
    color: #e4e5e8; 
}
.light-grey-to-white:hover { 
    color: rgba(255, 255, 255, 1); 
}
.wmp-tool-button.button-time{
    margin-right:2px;
    padding-left: 5px;
    padding-bottom: 4px;
}

.wmp-time-display{
    width: auto;
    font-family: "YouTube Noto",Roboto,Arial,Helvetica,sans-serif;
    color: #eee;
    text-align: left;
    direction: ltr;
    font-size: 14px;
    line-height: 1.3;
    -webkit-font-smoothing: antialiased;
    -webkit-tap-highlight-color: rgba(0,0,0,0);
}

.wmp-volume-slider{
    width: 52px;
}

.wmp-volume-slider-total-bar{
    height: 3px;
    width: 52px;
    margin-top: -3px;
    max-width: 52px;
    display: block;
    position: absolute;
}

.wmp-volume-slider-level-bar{
    height: 100%;
    background-color:white;
    display: block;
    position: absolute;
}

.wmp-volume-slider-left-bar{
    height: 100%;
    background-color: rgba(255,255,255,.2);
    display: block;
}

.wmp-volume-slider-scrubber-button{
    position: relative;
    height: 13px;
    width: 13px;
    background-color: rgb(255, 255, 255);
    border-radius: 50%;
    margin-top: -8px;
}
.wmp-progress-bar-wrapper{
    display: inline-block;
    height: 20px;
    bottom: 32px;
    position: absolute;
    z-index: 74;
    cursor: pointer;
    left: 12px;
    right: 12px;
}
.wmp-progress-bar{
    pointer-events: none;
    height: 3px;
    width:100%;
    background-color: rgba(255, 255, 255, 0.2);
    margin-top: 12px;
    position: absolute;
}

.wmp-progress-bar.desired{
    width:0%;
    background-color: rgba(255, 255, 255, 0.4);
    z-index: 2;
}

.wmp-progress-bar.progression{
    width:0%;
    z-index: 3;
}

.wmp-progress-bar.loaded{
    width:0%;
    background-color: rgba(255, 255, 255, 0.4);
    z-index: 1;
}

.wmp-progress-bar-wrapper-highighted{
    height: 25px;
    bottom: 30px;
}

.wmp-progress-bar-highighted{
    height: 5px;
    margin-top: 14px;
}
/*
.wmp-progress-bar:hover{
    height: 5px;
    margin-bottom: -1px;
}
*/
.wmp-scrubber-button{
    height: 13px;
    width: 13px;
    background-color: rgb(255, 255, 255);
    border-radius: 50%;
    display: inline-block;
    position: absolute;
    top: 10px;
    margin-left: -6.5px;
    z-index: 4;
}

.logo {
    width: 50px;
    vertical-align: middle;
}

.logo-image {
    max-width: 100px;
    max-height: 80%;
    width: auto;
    height: auto;
    margin-top: 1px;
}


.wmp-spinner {
    position: absolute;
    left: 50%;
    top: 50%;
    width: 64px;
    margin-left: -32px;
    z-index: 18;
    pointer-events: none
  }
  
  .wmp-spinner-container {
    pointer-events: none;
    position: absolute;
    width: 100%;
    padding-bottom: 100%;
    top: 50%;
    left: 50%;
    margin-top: -50%;
    margin-left: -50%;
    -webkit-animation: wmp-spinner-linspin 1568.23529647ms linear infinite;
            animation: wmp-spinner-linspin 1568.23529647ms linear infinite;
  }
  
  .wmp-spinner-rotator {
    position: absolute;
    width: 100%;
    height: 100%;
    -webkit-animation: wmp-spinner-easespin 5332ms cubic-bezier(0.4, 0.0, 0.2, 1) infinite both;
            animation: wmp-spinner-easespin 5332ms cubic-bezier(0.4, 0.0, 0.2, 1) infinite both
  }
  
  .wmp-spinner-left {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    overflow: hidden
  }
  
  .wmp-spinner-right {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    overflow: hidden
  }
  
  .wmp-spinner-left {
    right: 49%
  }
  
  .wmp-spinner-right {
    left: 49%
  }
  
  .wmp-spinner-circle {
    box-sizing: border-box;
    position: absolute;
    width: 200%;
    height: 100%;
    border-style: solid;
    border-color: #ddd #ddd transparent;
    border-radius: 50%;
    border-width: 6px
  }
  
  .wmp-spinner-left .wmp-spinner-circle {
    left: 0;
    right: -100%;
    border-right-color: transparent;
    -webkit-animation: wmp-spinner-left-spin 1333ms cubic-bezier(0.4, 0.0, 0.2, 1) infinite both;
            animation: wmp-spinner-left-spin 1333ms cubic-bezier(0.4, 0.0, 0.2, 1) infinite both;
  }
  
  .wmp-spinner-right .wmp-spinner-circle {
    left: -100%;
    right: 0;
    border-left-color: transparent;
    -webkit-animation: wmp-right-spin 1333ms cubic-bezier(0.4, 0.0, 0.2, 1) infinite both;
            animation: wmp-right-spin 1333ms cubic-bezier(0.4, 0.0, 0.2, 1) infinite both;
  }
  
  @-webkit-keyframes wmp-spinner-linspin {
    to {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg)
    }
  }
  
  @keyframes wmp-spinner-linspin {
    to {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg)
    }
  }
  
  @-webkit-keyframes wmp-spinner-easespin {
    12.5% {
      -webkit-transform: rotate(135deg);
              transform: rotate(135deg)
    }
    25% {
      -webkit-transform: rotate(270deg);
              transform: rotate(270deg)
    }
    37.5% {
      -webkit-transform: rotate(405deg);
              transform: rotate(405deg)
    }
    50% {
      -webkit-transform: rotate(540deg);
              transform: rotate(540deg)
    }
    62.5% {
      -webkit-transform: rotate(675deg);
              transform: rotate(675deg)
    }
    75% {
      -webkit-transform: rotate(810deg);
              transform: rotate(810deg)
    }
    87.5% {
      -webkit-transform: rotate(945deg);
              transform: rotate(945deg)
    }
    to {
      -webkit-transform: rotate(1080deg);
              transform: rotate(1080deg)
    }
  }
  
  @keyframes wmp-spinner-easespin {
    12.5% {
      -webkit-transform: rotate(135deg);
              transform: rotate(135deg)
    }
    25% {
      -webkit-transform: rotate(270deg);
              transform: rotate(270deg)
    }
    37.5% {
      -webkit-transform: rotate(405deg);
              transform: rotate(405deg)
    }
    50% {
      -webkit-transform: rotate(540deg);
              transform: rotate(540deg)
    }
    62.5% {
      -webkit-transform: rotate(675deg);
              transform: rotate(675deg)
    }
    75% {
      -webkit-transform: rotate(810deg);
              transform: rotate(810deg)
    }
    87.5% {
      -webkit-transform: rotate(945deg);
              transform: rotate(945deg)
    }
    to {
      -webkit-transform: rotate(1080deg);
              transform: rotate(1080deg)
    }
  }
  
  @-webkit-keyframes wmp-spinner-left-spin {
    0% {
      -webkit-transform: rotate(130deg);
              transform: rotate(130deg)
    }
    50% {
      -webkit-transform: rotate(-5deg);
              transform: rotate(-5deg)
    }
    to {
      -webkit-transform: rotate(130deg);
              transform: rotate(130deg)
    }
  }
  
  @keyframes wmp-spinner-left-spin {
    0% {
      -webkit-transform: rotate(130deg);
              transform: rotate(130deg)
    }
    50% {
      -webkit-transform: rotate(-5deg);
              transform: rotate(-5deg)
    }
    to {
      -webkit-transform: rotate(130deg);
              transform: rotate(130deg)
    }
  }
  
  @-webkit-keyframes wmp-right-spin {
    0% {
      -webkit-transform: rotate(-130deg);
              transform: rotate(-130deg)
    }
    50% {
      -webkit-transform: rotate(5deg);
              transform: rotate(5deg)
    }
    to {
      -webkit-transform: rotate(-130deg);
              transform: rotate(-130deg)
    }
  }
  
  @keyframes wmp-right-spin {
    0% {
      -webkit-transform: rotate(-130deg);
              transform: rotate(-130deg)
    }
    50% {
      -webkit-transform: rotate(5deg);
              transform: rotate(5deg)
    }
    to {
      -webkit-transform: rotate(-130deg);
              transform: rotate(-130deg)
    }
  }
.wmp-thumbnail-overlay-image {
